@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	font-size: 10px;
}

body {
	background-color: hsl(234, 29%, 20%);
	min-height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
}

#root {
	width: 100%;
}

.container {
	padding: 2.4rem;
	background-color: white;
	width: 55%;
	height: 50rem;
	border-radius: 2.4rem;
	display: flex;
	align-items: center;
	margin: auto;
	gap: 2.4rem;
}

.email-field {
	width: 55%;
	height: 100%;
	padding: 3.6rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	/* gap: 12px; */
}

.writing {
	font-size: 1.6rem;
	font-family: "Roboto", sans-serif;
	font-weight: 400;
}

.writing h1 {
	font-size: 5.4rem;
	font-weight: 700;
	margin-bottom: 1.2rem;
}
.writing p {
	margin-bottom: 1.2rem;
}

.writing ul {
	margin-left: 2.4rem;
	list-style-image: url("../assets/images/icon-list.svg");
	/* vertical-align: middle; */
}
.writing li {
	padding-left: 1.2rem;
	margin-bottom: 0.8rem;
}

.writing li:last-of-type {
	margin-bottom: 0;
}

.form {
	font-family: "Roboto", sans-serif;
	font-weight: 400;
}

.form input {
	width: 100%;
	padding-left: 1.6rem;
	height: 4.2rem;
	border: 1px solid hsl(231, 7%, 60%);
}

.form strong {
	font-size: 1.2rem;
}

.form p {
	margin-bottom: 0.8rem;
	display: flex;
	justify-content: space-between;
}

button {
	/* padding: 18px; */
	/* vertical-align: middle; */
	height: 4.2rem;
	background-color: hsl(235, 18%, 26%);
	border-radius: 0.8rem;
	color: white;
	font-size: 1.4rem;
	font-weight: 700;
}

.btn:hover,
.btn:active {
	border: none;
	background-color: hsl(4, 100%, 67%);
}

.image-field {
	width: 45%;
	height: 100%;
	background-image: url("../assets/images/illustration-sign-up-desktop.svg");
	/* border-radius: 0.8rem; */
}

/* ////////////////////////////////// */

.confirmation-container {
	padding: 3.2rem;
	background-color: white;
	width: 25%;
	height: 40rem;
	border-radius: 2.4rem;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	flex-direction: column;
	margin: auto;
	/* gap: 2.4rem; */
}

.confirmation-writing {
	text-align: center;
	font-size: 1.6rem;
	font-family: "Roboto", sans-serif;
	font-weight: 400;
}
.confirmation-writing h1 {
	font-size: 5.4rem;
	font-weight: 700;
	line-height: 5rem;
	margin-bottom: 1.2rem;
}

.dismiss-btn {
	width: 95%;
}

.dismiss-btn:hover,
.dismiss-btn:active {
	border: none;
	background-color: hsl(4, 100%, 67%);
}

/* /////////////////////////////////////////// */

/* HElper classes */
.invalid {
	color: red;
}

.vanish {
	display: none;
}

/* /////////////////////////////////////////// */

/* media querries */
@media (max-width: 1466px) {
	.container {
		width: 65%;
		padding: 1.8rem;
		/* gap: 1.8rem; */
	}
	.email-field {
		padding: 2.4rem;
	}
	.writing li {
		margin-bottom: 0;
	}
	.writing h1 {
		font-size: 4.8rem;
	}
}

@media (max-width: 1065px) {
	html {
		font-size: 9px;
	}
	.container {
		width: 70%;
	}
}

@media (max-width: 880px) {
	html {
		font-size: 8px;
	}
	.container {
		width: 80%;
		gap: 1.8rem;
	}
	.email-field {
		padding: 1.8rem;
	}
}

@media (max-width: 657px) {
	.container {
		width: 90%;
		gap: 0;
	}
	.email-field {
		width: 65%;
	}
	.image-field {
		width: 35%;
	}
	.writing h1 {
		font-size: 4.2rem;
	}
}

@media (max-width: 436px) {
	html {
		font-size: 9px;
	}
	.container {
		width: 100%;
		padding: 0;
		flex-direction: column-reverse;
		min-height: 100vh;
		border-radius: 0;
	}
	.email-field {
		width: 100%;
		height: 70%;
		justify-content: space-between;
	}
	.writing h1 {
		font-size: 3.8rem;
	}
	.image-field {
		width: 100%;
		height: 30%;
	}
}

@media (max-width: 244px) {
	html {
		font-size: 8px;
	}
	.email-field {
		padding: 1rem;
	}
}
